.div {
  height: 100vh;
  background-image: url(./../../assets/main.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.logo {
  background-color: brown;
  position: absolute;
  top: 1vh;
  right: 1vh;
}

.leftBottom {
  position: absolute;
  bottom: 20vh;
  left: 5vh;
}

.circularProgress {
  width: 3vw;
  margin-left: 0.5vw;
  margin-right: 0.5vw;
  display: inline-flex;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.circularImageBlink {
  animation: blink normal 2s infinite ease-in-out;
}

.leftBottomMain {
  position: absolute;
  top: 81vh;
  left: 29vh;
  color: #fff;
}

.leftBottomNav {
  position: absolute;
  bottom: 1.5vh;
  left: 29vh;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0.5vh;
  border-radius: 10px;
}

.lefBottomMic {
  position: absolute;
  bottom: 6vh;
  left: 29vh;
  color: white;
}
