.div {
  margin: auto;
  text-align: center;
  padding: auto;
  max-width: 140vh;
}

.card {
  margin-top: 10vh;
}

.legend {
  font-size: 18px;
}

.fieldsetOn {
  min-height: 70vh;
  transition: 0.5s ease;
  opacity: 1;
  text-align: center;
  color: #fff;
  background-size: cover;
  border-radius: 10px;
}

.fieldsetOn:hover {
  background-color: #fff;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6);
}

.fieldsetOn:hover .text {
  opacity: 1;
}

.text {
  opacity: 0;
  color: #fff;
  margin-top: 35vh;
}

.fieldsetOff {
  min-height: 70vh;
  transition: 0.5s ease;
  opacity: 1;
  text-align: center;
  color: #fff;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.8);
  border-radius: 10px;
}

.title {
  background-color: rgba(20, 20, 20, 0.8);
  color: #fff;
}

.lockedIcon {
  font-size: 6em;
  color: rgb(255, 255, 255);
  margin-top: 30vh;
}
