.mainCard {
  margin: auto;
  margin-top: 20vh;
  padding-left: auto;
  max-width: 100vh;
  text-align: center;
}

.gridItem {
  margin-left: 2vh;
  margin-right: 2vh;
  margin-top: 5vh;
  color: #fff;
}

.cardBody {
  border: 1px solid grey;
  color: #fff;
}

.rightColumn {
  margin-top: -30vh;
  color: #fff;
}

.button {
  margin-bottom: 2px;
  margin: auto;
  background-color: #303f9f;
  color: #fff;
}

.deleteButton {
  color: #ec384a;
}

.addCharIcon {
  background: linear-gradient(60deg, #303f9f, #1565c0);
  border-radius: 20%;
}

.textfield {
  margin-bottom: 1vh;
}
